import React from "react";
import { Button, ExpansionList, ExpansionPanel } from 'react-md';
import Helmet from "react-helmet";
import Card from "react-md/lib/Cards";
import Link from "gatsby-link";
import CardText from "react-md/lib/Cards/CardText";
import PostTags from "../components/PostTags/PostTags";
import PostCover from "../components/PostCover/PostCover";
import PostInfo from "../components/PostInfo/PostInfo";
import SocialLinks from "../components/SocialLinks/SocialLinks";
import PostSuggestions from "../components/PostSuggestions/PostSuggestions";
import SEO from "../components/SEO/SEO";
import config from "../../data/SiteConfig";

import "./b16-tomorrow-dark.css";
import "./post.scss";

export default class PostTemplate extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      mobile: true
    };
    this.handleResize = this.handleResize.bind(this);
  }
  componentDidMount() {
    this.handleResize();
    window.addEventListener("resize", this.handleResize);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.handleResize);
  }

  handleResize() {
    if (window.innerWidth >= 640) {
      this.setState({ mobile: false });
    } else {
      this.setState({ mobile: true });
    }
  }

  render() {
    const { mobile } = this.state;
    const { slug } = this.props.pathContext;
    const expanded = !mobile;
    const postOverlapClass = mobile ? "post-overlap-mobile" : "post-overlap";
    const fileEdges = this.props.data.allFile.edges;
    const postNode = this.props.data.markdownRemark;
    const post = postNode.frontmatter;
    if (!post.id) {
      post.id = slug;
    }
    if (!post.category_id) {
      post.category_id = config.postDefaultCategoryID;
    }

    const coverHeight = mobile ? 180 : 350;
    return (
      <div className="post-page md-grid md-grid--no-spacing">
        <Helmet>
          <title>{`${post.title} | ${config.siteTitle}`}</title>
          <link rel="canonical" href={`${config.siteUrl}${post.id}`} />
        </Helmet>
        <SEO postPath={slug} postNode={postNode} postSEO />
        <PostCover
          postNode={postNode}
          coverHeight={coverHeight}
          coverClassName="md-grid md-cell--9 post-cover"
          fileEdges={fileEdges}
        />
        <div
          className={`md-grid md-cell--9 post-page-contents mobile-fix ${postOverlapClass}`}
        >
          <Card className="md-grid md-cell md-cell--12 post">
            <CardText className="post-body">
              <h1 className="md-display-2 post-header">{post.title}</h1>
              <PostInfo postNode={postNode} />
              <div className="post-box">
                {
                  post.description.split('\n').map( line => (<span>{line}<br /></span>))
                }
              </div>
            </CardText>
          </Card>
          <Card className="md-grid md-cell md-cell--12 post">
            <CardText className="post-body">
              <div className="post-box">
                <h2> </h2>
                {post.lessonSummary &&
                  post.lessonSummary.map(item => (
                    <dl key={item.title}>
                      <dt>{item.title}</dt>
                      <dd>
                        {
                          item.description.split('\n').map( line => (<span>{line}<br /></span>))
                        }
                      </dd>
                    </dl>
                  ))
                }
              </div>
            </CardText>
          </Card>
          <Card className="md-grid md-cell md-cell--12 post lessons">
            <CardText className="post-body lessons-body">
              <div className="post-box">
                <h2> </h2>
                <ExpansionList>
                  {post.lessons &&
                    post.lessons.map(lesson => (
                      <ExpansionPanel key={lesson.title} label={lesson.title}  footer={null}>
                        <p>
                          {
                            lesson.description.split('\n').map( line => (<span>{line}<br /></span>))
                          }
                        </p>
                      </ExpansionPanel>
                    ))
                  }
                </ExpansionList>
              </div>
            </CardText>
          </Card>
          <Card className="md-grid md-cell md-cell--12 post">
            <CardText className="post-body">
              <div className="post-box">
                <h2></h2>
                <div className="carousal">
                  <img src={`/assets/${post.images[0]}`} alt="  " />
                  <img src={`/assets/${post.images[1]}`} alt="  " />
                  <img src={`/assets/${post.images[2]}`} alt="  " />
                  <img src={`/assets/${post.images[3]}`} alt="  " />
                  <img src={`/assets/${post.images[4]}`} alt="  " />
                </div>
              </div>
            </CardText>
          </Card>
          <Card className="md-grid md-cell md-cell--12 post">
            <CardText className="post-body">
              <div className="post-box">
                <h2> </h2>
                <ul>
                  {post.notices &&
                    post.notices.map(notice => (
                      <li key={notice}>{notice}</li>
                    ))
                  }
                </ul>
              </div>
              <div dangerouslySetInnerHTML={{ __html: postNode.html }} />
            </CardText>
            <div className="post-meta">
              {/* <PostTags tags={post.tags} /> */}
              <SocialLinks
                postPath={slug}
                postNode={postNode}
                mobile={this.state.mobile}
              />
            </div>
            <Link
              className="button-back"
              to="/"
            >
              <Button floating primary iconClassName="fa fa-arrow-left" />
            </Link>
          </Card>
        </div>
      </div>
    );
  }
}

/* eslint no-undef: "off" */
export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      timeToRead
      excerpt
      frontmatter {
        title
        cover
        images
        startAt
        place
        tags
        description
        lessonSummary {
          title
          description
        }
        lessons {
          title
          description
        }
        notices
      }
      fields {
        slug
        date
      }
    }
    allFile {
      edges {
        node {
          id
          absolutePath
          childImageSharp {
            id
            resolutions {
              base64
              tracedSVG
              aspectRatio
              width
              height
              src
              srcSet
              srcWebp
              srcSetWebp
              originalName
            }
            internal {
              contentDigest
              type
              owner
            }
            sizes(maxWidth: 1240) {
              ...GatsbyImageSharpSizes
              originalName
            }
          }
        }
      }
    }
  }
`;



